.list {
    color: white;
    width: 300px;
    height: 50px;
    background-color:rgb(155, 155, 155);
    margin: 20px auto;
    border-radius: 5px;
} 
.list p {
    padding: 10px;
    position: relative;
}

.list p span {
    position: absolute;
    right: 10px;
    cursor: pointer;
}

.icon {
    margin: 0 5px;
    color: aqua;
    font-size: 15px;
}

.list p input {
    background-color: transparent;
    border: 0;
    color: white;   
    font-size: 15px;
}

.list p input:focus {
    outline: none;
}