body {
  background-color: rgb(168, 185, 243);
}

.App {
  background-color: wheat;
  min-height: 500px;
  width: 400px;
  margin: 40px auto;
}

#todo-form input {
  background-color: burlywood;
  height: 50px;
  width: 200px;
  padding: 0 10px;
  margin: 20px;
  font-size: 15px;
  border-style: none;
  border-radius: 5px;
  color: whitesmoke;
}

#todo-form button {
  height: 50px;
  width: 80px;
  border-radius: 5px;
  border-style: none;
  background-color: rgb(155, 133, 77);
  cursor: pointer;
  
}

